<template>
  <div class="game" @click="deselect" v-touch:swipe.left="swipedLeft">
    <div class="game_start" v-html="i18n.tap">
    </div>
    <GamePlayers />
    <div
      class="game_board"
      :class="['game_board-size-' + game.settings.size,
      {'game_board-busy' : game.busy}]"
      @click="boardClick"
    >
      <div
        v-for="(cell, i) in game.board"
        :key="i"
        class="game_cell"
        :class="[
        {'game_cell-selectable' : selectable(i)},
        {'game_cell-selected' : i === game.selected},
        {'game_cell-connectTop' : connectTop(i, cell.owner)},
        {'game_cell-connectRight' : connectRight(i, cell.owner)},
        {'game_cell-connectBottom' : connectBottom(i, cell.owner)},
        {'game_cell-connectLeft' : connectLeft(i, cell.owner)},
        'game_cell-defender-' + isAttacked(i),
        'game_cell-owner-' + cell.owner]"
        @click="select(i, cell.owner)"
        :depressed="i !== game.selected"
        v-ripple
      >
        <div class="game_cell_dices">
          <div class="game_cell_dices_left">
            <div v-for="(dice, i) in Math.min(cell.dice, 4)" :key="i" class="game_cell_dice"></div>
          </div>
          <div v-if="cell.dice > 4" class="game_cell_dices_right">
            <div v-for="(dice, i) in cell.dice - 4" :key="i" class="game_cell_dice"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import GamePlayers from './GamePlayers.vue'

export default {
  name: 'Game',
  components: {
    GamePlayers
  },
  computed: mapState({
    game: state => state.game,
    i18n: state => state.i18n
  }),
  data: () => ({
    gameService: new GameService()
  }),
  methods: {
    select (i, owner) {
      if (i === this.game.selected) {
        this.deselect()
      } else if (owner === this.game.player.id) {
        this.gameService.changeSelected(i)
      } else {
        this.gameService.attack(i)
      }
    },
    selectable (i) {
      const cell = this.game.board[i]
      if (cell.owner === this.game.player.id) {
        if (cell.dice > 1) {
          return true
        }
      } else if (this.game.selected !== null) {
        if (i === this.game.selected) {
          return true
        }
        if (this.game.board[this.game.selected].adjacents.includes(i)) {
          return true
        }
      }
      return false
    },
    boardClick (e) {
      e.stopPropagation()
      if (this.game.status < 2 && !this.game.player.isPlayer) {
        this.gameService.startGame()
      }
    },
    deselect () {
      if (this.game.board.length < 1) {
        this.gameService.newGame()
        this.changeShowSettings(false)
      } else {
        this.changeSelected(null)
      }
    },
    connectTop (i, owner) {
      const checkId = i - this.game.settings.size
      if (
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return true
      }
      return false
    },
    connectRight (i, owner) {
      const checkId = i + 1
      if (
        checkId % this.game.settings.size &&
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return true
      }
      return false
    },
    connectBottom (i, owner) {
      const checkId = i + this.game.settings.size
      if (
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return true
      }
      return false
    },
    connectLeft (i, owner) {
      const checkId = i - 1
      if (
        i % this.game.settings.size &&
        this.game.board[checkId] &&
        this.game.board[checkId].owner === owner
      ) {
        return true
      }
      return false
    },
    isAttacked (i) {
      if (i !== this.game.defender) {
        return false
      }
      if (this.game.defender + 1 === this.game.selected) {
        return 'right game_cell-defender'
      }
      if (this.game.defender - 1 === this.game.selected) {
        return 'left game_cell-defender'
      }
      if (this.game.defender < this.game.selected) {
        return 'bottom game_cell-defender'
      }
      if (this.game.defender > this.game.selected) {
        return 'top game_cell-defender'
      }
    },
    swipedLeft () {
      if (this.game.status === 2 && this.game.player.isPlayer) {
        this.gameService.nextPlayer()
      }
    },
    ...mapActions('game', ['changeSelected']),
    ...mapActions('settings', ['changeShowSettings'])
  }
}
</script>

<style scoped lang="scss">
.game {
  padding: 20px 0 60px;
  position: relative;
  &_start {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--player-color-1);
    font-size: 64px;
    text-align: center;
    line-height: 72px;
    text-shadow: 1px 1px #fff;
  }
  &_board {
    display: grid;
    grid-auto-rows: 1fr;
    border: 4px solid var(--border-color-light);
    margin: 0 auto;
    border-radius: 25px;
    position: relative;
    max-width: calc(100vw - 30px);
    max-height: calc(100vw - 30px);
    opacity: 1;
    column-gap: 4px;
    row-gap: 4px;
    background-color: var(--border-color-light);
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    &-size {
      &-4 {
        width: 404px;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(15.5vw - 2px);
            max-height: calc(25vw - 19px);
            &_left {
              max-height: calc(25vw - 19px);
            }
            &_right {
              max-height: calc(25vw - 19px);
            }
          }
          &_dice {
            max-height: calc(6.25vw - 1px);
            max-width: calc(6.25vw - 1px);
          }
          &:nth-child(4) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(4) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-left-radius: 21px;
            }
          }
        }
      }
      &-5 {
        width: 504px;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(10vw - 2px);
            max-height: calc(20vw - 16px);
            &_left {
              max-height: calc(20vw - 16px);
            }
            &_right {
              max-height: calc(20vw - 16px);
            }
          }
          &_dice {
            max-height: 5vw;
            max-width: 5vw;
          }
          &:nth-child(5) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(5) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-right-radius: 21px;
            }
          }
        }
      }
      &-6 {
        width: 604px;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(8.333333333333333vw - 2px);
            max-height: calc(16.66666vw - 16px);
            &_left {
              max-height: calc(16.66666vw - 16px);
            }
            &_right {
              max-height: calc(16.66666vw - 16px);
            }
          }
          &_dice {
            max-height: 4.166666666666666vw;
            max-width: 4.166666666666666vw;
          }
          &:nth-child(6) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(6) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-right-radius: 21px;
            }
          }
        }
      }
    }
    &:empty {
      opacity: 0;
      pointer-events: none;
    }
    &-busy {
      .game_cell {
        pointer-events: none;
      }
    }
  }
  &_cell {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 0;
    pointer-events: none;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.1);
    transition: all 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    border-radius: 4px;
    height: 100px;
    box-sizing: content-box;
    &:before {
      content: "";
      bottom: 0;
      left: 0;
      right: 0;
      top: 0;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      box-shadow: 0 0 0 2px $errorColor, 0 0 4px 4px $errorColor;
      z-index: 1;
      border-radius: 4px;
      transition: opacity 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    }
    &:first-child {
      border-top-left-radius: 21px;
      &:before {
        border-top-left-radius: 21px;
      }
    }
    &:last-child {
      border-bottom-right-radius: 21px;
      &:before {
        border-bottom-right-radius: 21px;
      }
    }
    &-connectTop {
      border-top-width: 2px;
      margin-top: -2px;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
    &-connectRight {
      border-right-width: 2px;
      margin-right: -2px;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
    &-connectBottom {
      border-bottom-width: 2px;
      margin-bottom: -2px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &-connectLeft {
      border-left-width: 2px;
      margin-left: -2px;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    &-selected {
      background-color: var(--text-color-dark) !important;
      color: #fff !important;
      &:before{
        opacity: 1;
      }
    }
    &-defender {
      &:after {
        content: "";
        position: absolute;
        z-index: 9;
        width: 50px;
        height: 50px;
        background-size: contain;
      }
      &-top {
        &:after {
          top: -25px;
          background-image: url("../assets/images/attack-bottom.svg");
        }
      }
      &-right {
        &:after {
          right: -25px;
          background-image: url("../assets/images/attack-left.svg");
        }
      }
      &-bottom {
        &:after {
          bottom: -25px;
          background-image: url("../assets/images/attack-top.svg");
        }
      }
      &-left {
        &:after {
          left: -25px;
          background-image: url("../assets/images/attack-right.svg");
        }
      }
    }
    &-selectable {
      pointer-events: all;
      cursor: pointer;
    }
    &_dices {
      display: flex;
      flex-flow: column wrap;
      width: 42px;
      height: 84px;
      align-items: center;
      justify-content: flex-end;
      position: absolute;
      &_left {
        height: 84px;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
      &_right {
        height: 84px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
      }
    }
    &_dice {
      background-image: url("../assets/images/dice-5.svg");
      height: 25px;
      width: 25px;
      margin: -2px;
      background-size: contain;
    }
  }
  @each $i, $color in $colors {
    &_cell {
      &-owner {
        &-#{$i} {
          background-color: var(--player-color-#{$i});
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .game {
    &_board {
      width: 404px;
      &-size-4 {
        .game_cell {
          height: 91px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 72px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 61.33px;
        }
      }
    }
  }
}
@media (max-width: 410px) {
  .game {
    &_board {
      width: 335px;
      &-size-4 {
        .game_cell {
          height: 78.75px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 64.19px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 53.16px;
        }
      }
    }
  }
}
@media (max-width: 374px) {
  .game {
    &_board {
      width: 330px;
      &-size-4 {
        .game_cell {
          height: 77.5px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 63.19px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 50.33px;
        }
      }
    }
  }
}
@media (max-width: 359px) {
  .game {
    &_board {
      width: 290px;
      &-size-4 {
        .game_cell {
          height: 67.5px;
        }
      }
      &-size-5 {
        .game_cell {
          height: 53.19px;
        }
      }
      &-size-6 {
        .game_cell {
          height: 43.66px;
        }
      }
    }
  }
}
</style>
